import React, {  useState, useEffect } from 'react';
import $ from "jquery";
import "./Topnav.css";

const Topnav = ({
  movePage
}) => {
  const [modal, setModal] = useState();
  const [className, setClassName] = useState("layer_popup");
  const div = document.querySelector("#main");

  const modalUp = () => {
    setModal(true);
  }
  const modalOff = () => {
    setModal(false);
  }
  useEffect(() => {
    if (modal)
      setClassName(className + ' on');
    else if (modal == false)
      setClassName('layer_popup');
  }, [modal])



  return (
    <nav className="topnav_background" >
      <div className="topnav">
        <a className="leftAtt" href="https://thecgawards.kro.kr">
          <img className="Title" src="image/Title.png"></img>
          <img className="Logo" src="image/logo.png"></img>
          </a>

        <div onClick={() => movePage(null)} className="Button">
          <a id="topnav_Button">투표하러 가기</a>
          <img style={{position:"absolute"}}src="image/Scroll_button.png"></img>
          </div>
      
        <ul className="topnav_icon">
          <li><a href="https://www.youtube.com/channel/UCCM8oRwSn5KdywxiuLp-bfA" target="_blank">
            <img className="Youtube" src="image/YoutubeIcon.png"></img></a></li>
          <li><a href="https://www.instagram.com/kah_best_of_game/" target="_blank">
            <img src="image/InstaIcon.png"></img></a></li>
          <li><a href="https://www.facebook.com/anigoGame" target="_blank">
            <img src="image/FacebookIcon.png"></img></a></li>
        </ul>

        <ul className="topnav_icon_small">
          <li onClick={modalUp}><a>
            <img src="menu.svg"></img></a></li>
        </ul>

      </div>
      <div className={className}>
        <div className="layer">
          <div className="menu_bar">
            <ul className="menu_item">
              <li> 
                <a className="link" onClick={() => {movePage(1); modalOff();}}>게임 정보</a> 
              </li>
              <li> 
                <a className="link" onClick={() => {movePage(2); modalOff();}}>투표 하기</a> 
              </li>
              <li style={{paddingTop: '20px'}}> 
                <a href="https://www.youtube.com/channel/UCCM8oRwSn5KdywxiuLp-bfA" target="_blank">
                  <img style={{ width: '50px' }} src="image/YoutubeIcon.png"></img></a>
                <a href="https://www.instagram.com/kah_best_of_game/" target="_blank">
                  <img src="image/InstaIcon.png"></img></a>
                <a href="https://www.facebook.com/anigoGame" target="_blank">
                  <img src="image/FacebookIcon.png"></img></a>
              </li>

              <li style={{paddingTop:'20px'}}>
                <p>만든 사람</p>
              </li>
              <li> <a>웹 개발: 20기 박영진</a> </li>
              <li> <a>디자인: 20기 이지민</a> </li>
            </ul>
           
          </div>
          <div className="btn_area">
          </div>
          <button className="btn_close" onClick={modalOff}>CLOSE</button>
        </div>
        <div className="dimmed">
        </div>
      </div>
    </nav>

  );
}




export default Topnav;
